<template>
  <div  class="my-clas">
   <div class="container pad1">
    <div class="row align-items-end text-center block-elephants-logo">
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-inline d-lg-inline d-xl-inline">
        <img src="images/2.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-sm-inline d-md-inline d-lg-inline d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-inline d-md-none d-lg-inline d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-none d-lg-none d-xl-inline">
        <img src="images/1.jpg" alt="elephant">
      </div>
      <div class="logo-alephants col-md col-sm col-lg col-xl d-none d-sm-none d-md-inline d-lg-inline d-xl-inline">
        <img src="images/2.jpg" alt="elephant">
      </div>
    </div>
    <div class="row justify-content-md-center col-auto my-h-class">
      <h1>Добро пожаловать в систему тестирования ELEPHANTS</h1>
    </div>  
    <div class="row justify-content-center col-auto pad2">
        <button type="submit" name="#" value="#" data-toggle="modal" data-target=".bd-example-modal-sm">Пройти тест</button>
    </div>
  </div>
  <v-footer class="my-foo"> 
    <div class="container">
    <div class="row justify-content-center align-items-center footer-my">
      <div class="col-auto">
        <p>Powered by</p>
      </div>
      <div class="col-auto">
        <img src="images/logo.jpg" alt="logo">
      </div>
    </div>
  </div>
 </v-footer>
   
  <!-- Modal -->
<div class="modal fade bd-example-modal-sm" tabindex="-1" role="dialog" aria-labelledby="mySmallModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-sm">
    <div class="modal-content">
      <div class="l-auth">
      <v-form v-model="validLogin">
        <v-text-field label="Username"
                      v-model="credentials.username"
                      prepend-icon="account_box"
                      :rules="rules"
                      required
                      color="light-blue lighten-1">
        </v-text-field>

        <v-text-field label="Password"
                      v-model="credentials.password"
                      prepend-icon="lock"
                      :rules="rules"
                      :append-icon="loginPasswordVisible ? 'visibility' : 'visibility_off'"
                      :append-icon-cb="() => (loginPasswordVisible = !loginPasswordVisible)"
                      :type="loginPasswordVisible ? 'text' : 'password'"
                      color="light-blue lighten-1"
                      required>
        </v-text-field>
        <v-btn color="light-blue lighten-1" @click.native="submitAuthentication()">Войти</v-btn>
      </v-form>
      <v-snackbar timeout="6000"
                bottom="bottom"
                color="red lighten-1"
                v-model="snackbar">
      {{ message }}
    </v-snackbar>
    </div>
    </div>
  </div>
</div>

 </div>
</template>
<script>
/*всё начинается с импорта файла index.js из папки Authentication, так как нам нужен метод authenticate, определённый внутри этого файла.
snackbar: используется для панели сообщений.
validLogin: используется для проверки формы входа в систему.
validSignUp: используется для проверки формы регистрации.
signUpVisible: используется для вывода формы регистрации (при установке в true).
loginPasswordVisible: указывает на то, может ли пользователь видеть пароль, вводимый в форме входа в систему.
signUpPasswordVisible: указывает на то, можно ли видеть пароль, вводимый в форме регистрации.
rules: правила проверки данных, введённых в поля форм.
credentials: объект, привязанный к полям ввода формы входа в систему, используемый для аутентификации пользователя.
newUser: объект, привязанный к полям ввода формы регистрации в системе.
message: используется для вывода сообщений в ходе аутентификации.

В этом компоненте есть пара методов. В методе submitAuthentication осуществляется вызов метода authenticate из файла Authentication, с передачей контекста, учётных данных и пути для перенаправления. Метод submitSignUp используется для вызова метода signup.*/
  import Authentication from '../../../components/pages/Authentication'
  export default {
    data () {
      return {
        snackbar: false,
        validLogin: false,
        loginPasswordVisible: false,
        rules: [ (value) => !!value || 'This field is required' ],
        credentials: {
          username: '',
          password: ''
        },
        message: ''
      }
    },
/*В методе submitAuthentication осуществляется вызов метода authenticate из файла Authentication, с передачей контекста, учётных данных и пути для перенаправления. Метод submitSignUp используется для вызова метода signup.*/
    methods: {
      submitAuthentication () {
        Authentication.authenticate(this, this.credentials, '/')
      }
    }
  }
</script>
<style lang="scss" scoped>
  @import "./../../../assets/styles";
.l-auth {
    background-color: $background-color;
    padding: 15px;
  }
.my-foo {
  background: none;
}
</style>


