import Vue from 'vue'
import Router from 'vue-router'
//В этой строке мы импортируем файл Authentication, называя его Auth, так как компонент Authentication также был импортирован.
import * as Auth from '../components/pages/Authentication'
// Pages
//Тут мы сначала импортируем компонент Home
import Authentication from '../components/pages/Authentication/Authentication'
// Global components
// затем — Header
/*регистрируем его, помня о том, что знак @
при использовании webpack является псевдонимом для папки src. 
App-header — это имя тега, который мы будем использовать для вывода компонента Header.*/


// Register components

//обавить компонент BudgetList в маршрутизатор:



Vue.use(Router)
/*мы убираем meta из маршрута страницы Home.*/
/*Тут мы даём имя объекту Router для того, чтобы позже создать защиту системы навигации. 
Также мы добавляем путь к компоненту Home. В параметре meta.requiredAuth будет записано true. 
Это означает, что если к этому компоненту попытается получить доступ неаутентифицированный пользователь, 
он будет перенаправлен на страницу входа в систему.*/
/*Здесь мы указываем на то, что компонентом по умолчанию 
для домашней страницы является Home, 
а также включаем в эту страницу компонент Header.
Компонент Header, представляющий шапку страницы, нам там не нужен.*/
const router = new Router({
  routes: [
    {
      path: '/login',
      name: 'Authentication',
      component: Authentication
    }
  ]
})
/*Здесь мы защищаем систему навигации. 
А именно, регистрируем глобальный сторожевой хук, 
пользуясь которым перед переходом по каждому маршруту проверяем, 
установлен ли его параметр meta.requiredAuth в true. 
Если это так, мы проверяем объект пользователя из Authentication. 
Если пользователь не аутентифицирован, мы перенаправляем его на страницу входа в систему.*/
router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    if (Auth.default.user.authenticated) {
      next()
    } else {
      router.push('/login')
    }
  } else {
    next()
  }
})
//Этой командой экспортируем маршрутизатор.
export default router
//Без этого, если пользователь перезагрузит страницу или закроет её, а потом снова откроет, он будет перенаправлен на страницу входа в систему.


